.chat {
display: flex;
align-items: center;
justify-content: space-between;
padding: 20px;
height: 70px;
border-bottom: 1px solid lightgray;
}
.chat__details{
    flex:1;
}
.chat__image{
    margin-right: 20px;
}
.chat__details>p{
    color: gray;
}
.chat__timestamp{
    color: lightgray;
}